import React from 'react'
import MealPlan from './meal-plan.component';
import '../../css/fuel.styles.css';

function MealPlans({fetchPlanRoute, fetchPlan, mealPlanData, showData, mealPlans, planRoute, cuisine, type, diet, readR, selections}) {

    console.log(`Object.entries(planRoute): ${Object.entries(planRoute)}`);

    if (Object.entries(planRoute).length === 1) {
        console.log(`Object.entries(planRoute): ${Object.entries(planRoute).length}`);
        
        Object.keys(planRoute[Object.keys(planRoute)]).map((route) => {
            console.log(`Object.keys(planRoute[Object.keys(planRoute)][route]): ${Object.keys(planRoute[Object.keys(planRoute)][route])}`);
        })
    }

    if (mealPlanData) {
        console.log(`mealPlanData %c ${Object.entries(mealPlanData)} `,'background:green; color:black; font-weight:bold');
    }

    // for (let i = 0; i <= Object.keys(planRoute[Object.keys(planRoute)]).length-1; i++) {
    //     console.log(Object.keys(planRoute[Object.keys(planRoute)][i]));
    // }

    // EQUIVALENT


  return (
        <div className="meal-plan">
            {/* mexican / japanese / indian / greek // snack / entree / salad / soup // restrictions / metrics */}
            <div className="selections">
                    {Object.keys(planRoute[Object.keys(planRoute)]).map((route, index) => {
                        return <p 
                                key={index} 
                                id={`${index}${Object.keys(planRoute[Object.keys(planRoute)][route])}`} 
                                className="selection"
                                onClick={showData}
                                >
                                    {Object.keys(planRoute[Object.keys(planRoute)][route])}
                                </p>
                    })}     

                    {/* {Object.values(planRoute[Object.keys(planRoute)][0]).map((selection, index) => {
                        // {currentSelections[currentSelections.length - 1].map((selection, index) => {
                        return <p key={index} id={selection} className="selection" onClick={showData}>{selection}</p>
                    })} */}
            </div>
            {mealPlanData &&
                <div id="meal-plan-data" className="meal-plan-flow">
                    {Object.values(mealPlanData).map((data, index) => {
                        return <>
                                    {/* <p key={`key${index}`}>{Object.keys(data)}</p> */}
                                    <p key={`value${index}`} className="meal-plan-data-tidbit">{Object.values(data)}</p>
                                </>
                    })}
                </div>
            }
        </div>
    )
  }
  
  export default MealPlans

