import './css/App.css';
import Email from './assets/email.svg';
import GitHub from './assets/github.svg';
import LinkedIn from './assets/linkedin.svg';
import { createContext, useState, useRef } from 'react';
import { Routes, Route, Link } from 'react-router-dom';
import Home from './routes/home';
import Budget from './routes/budget';
import Errands from './routes/errands';
import Fuel from './routes/fuel/fuel';
import sources from './data/fuel.json';
import plans from './data/meals.json';
import resume from './data/resume.json';
import SignIn from './routes/sign-in';
import error from './assets/404\ error.svg';

export const MealContext = createContext();

function App() {

  // INTRODUCTION
  
  const intro = {
    type: "intro",
    title: "Hi,",
    body: ["I'm Peter, lover of cats, art & computers.", "Exploring different ideas is where my attention gravitates.", "Venturing through new territories of interactivity to enhance life for greater good.", "Creating imaginative functions in fun forms."]
  }
  const [contentType, setContentType] = useState(intro);

  const signIn = {
    type: "signIn",
    title: "Sign IN",
    body: [
    <input id="username" className="signIn-input"/>,
    <input id="password" className="signIn-input"/>,
    <button>Sign IN</button>
  ]
  }

  const codingSkills = {
    type: "list",
    title: "Skills",
    body: [
        "HTML",
        "CSS",
        "JavaScript",
        "React",
        "CLI",
        "Python",
        "SQL"
    ]
  }

  const contactDetails = {
    type: "link",
    title: "Contact",
    body: [
      <a className="mailtoui" href="mailto:peterpczerniak@outlook.com" target="_blank" rel="noreferrer noopener"><img className="contact-logo" alt="email symbol" src={Email}/></a>,
      <a className="contact-link" href="https://www.linkedin.com/in/peter-czerniak-48030b83/" target="_blank"><img className="contact-logo" alt="LinkedIn link" src={LinkedIn}/></a>,
      <a className="contact-link" href="https://github.com/peeczi/peeczi.github.io" target="_blank"><img className="contact-logo" alt="GitHub" src={GitHub}/></a>
    ]
  };

  const projects = {
    type: "list",
    title: "Projects",
    body: [
      <Link className="link-item" to="/fuel">Fuel Up On...</Link>,
      <Link className="link-item" to="/errands">Airin' My Errands</Link>,
      <Link className="link-item" to="/budget">No Budgin' with My Budgetin'</Link>
    ]
  }

  const changeContent = (event) => {
    const contentSelect = event.target.id;
    switch(contentSelect) {
      case 'profile-pic':
        setContentType(intro);
        break;
      case 'signIn':
        setContentType(signIn);
        break;
      case 'skills':
        setContentType(codingSkills);
        break;
      case 'projects':
        setContentType(projects);
        break;
      case 'resume':
        setContentType(resume);
        break;
      case 'contact':
        setContentType(contactDetails);
        break;
      default:
        console.error("Switch case error");
    }
  }

  // NO BUDGIN' WITH MY BUDGETIN'
  const [budget, setBudget] = useState();
  const [showBudgetInfo, setShowBudgetInfo] = useState(false);
  const [showExpenseInfo, setShowExpenseInfo] = useState(false);
  const [showAllCategories, setShowAllCategories] = useState(false);
  const [showFocusCategory, setShowFocusCategory] = useState(false);
  const [focusCategory, setFocusCategory] = useState();
  const expenseItemRef = useRef();
  const expenseAmountRef = useRef();
  const expenseCategoryRef = useRef();
  const [showCalculator, setShowCalculator] = useState(false);
  const [calculation, setCalculation] = useState();
  const [dividend, setDividend] = useState();
  const [divisor, setDivisor] = useState();
  const [operator, setOperator] = useState();

  // const [expense, setExpense] = useState({
  //     "home": [{"soap": 6}, {"floor mat": 7}, {"throw": 8}],
  //     "school": [{"ruler": 5}, {"folders": 7}, {"eraser": 8}],
  //     "medical": [{"syringe": 4}, {"band aids": 7}, {"gauze": 8}],
  //     "work": [{"computer": 9}, {"calculator": 7}, {"binder": 8}],
  //     "car": [{"engine oil": 19}, {"coolant": 27}, {"filter": 38}],
  //     "leisure": [{"tennis racket": 200}, {"athletic shorts": 47}, {"shoes": 180}],
  //     "other": [{"twine": 6}, {"container": 7}, {"magnet": 18}]
  //   });

  const [expense, setExpense] = useState({
    "home": [{}],
    "school": [{}],
    "medical": [{}],
    "work": [{}],
    "car": [{}],
    "leisure": [{}],
    "other": [{}],
    "test": [{}]
  });

  // feature to add: bill payments
  // const [bills, setBills] = useState({
  //   "tuition": {
  //     "amount": 273.52,
  //     "due day": 1
  //   },
  //   "phone": {
  //     "amount": 97,
  //     "due day": 21
  //   },
  //   "car insurance": {
  //     "amount": 160,
  //     "due day": 8
  //   },
  //   "visa": {
  //     "amount": 60,
  //     "due day": 15
  //   }
  // })

  const budgetEnter = (event) => {
    event.preventDefault();
      const budgetAmount = event.target.value;
      if (event.key === "Enter" && budgetAmount > 0) {
        setBudget(budgetAmount);
        setShowBudgetInfo(true);
      }
  }

  const handleBudgetSubmit = (event) => {
    event.preventDefault();
    setBudget("");
  }

  // add expense entry
  const handleExpenseSubmit = (event) => {
    event.preventDefault();
    
    const expenseCategory = String(expenseCategoryRef.current.value);

    let xpItem = expenseItemRef.current.value;
    let xpAmount = Number(expenseAmountRef.current.value);

    if (xpItem !== '' && xpAmount > 0){
      // SET EXPENSE ACCORDING TO
      if (Object.entries(expense[expenseCategory][0]).length === 0) {
        console.log(`HIIIIII!!! expense.${expenseCategory}[0]: %c${Object.entries(expense[expenseCategory][0]).length}`,'color:red');
        
          setExpense(prevExpense => ({
            ...prevExpense,
            [expenseCategory]:
            [{[xpItem]:xpAmount}]
          }))
        
      } else {
        setExpense(prevExpense => ({
        ...prevExpense,
        [expenseCategory]:  [
          ...prevExpense[expenseCategory],
            {[xpItem]:xpAmount}
          ]
      }))}
      setShowExpenseInfo(true);
      
      if (showAllCategories === false && showFocusCategory === false && focusCategory === undefined){
        setShowFocusCategory(true);
        setFocusCategory(expenseCategory);
      }
    }
  }

  function calculateCategorySubtotal(expenseCategory) {
    if (Object.entries(expenseCategory[0]).length > 0){
      let subtotal = 0;
      Object.values(expenseCategory).map((expenseCategoryExpense) => {
        subtotal += Number(Object.values(expenseCategoryExpense));
        return subtotal;
      })
      return subtotal;
    } 
  }

  function calculateExpenseTotal() {
    let expenseTotal = 0;
    Object.entries(expense).map((expenseCategory) => {
      const expenseTotalCalc = Object.values(expenseCategory[1]).map((category) => {
        expenseTotal += Number(Object.values(category));
        return expenseTotal;
      })
      return expenseTotalCalc[expenseTotalCalc.length-1];
    });
      return expenseTotal;
  }
  
  const expenseTotal = calculateExpenseTotal();

    // show calculator button state
  function showCALC() {
    setShowCalculator(prevState => !prevState);
  }

  // calculator functions
  function calculate(event) {
    const buttonId = event.target.id;
    const buttonValue = event.target.value;
    const isButtonNumber = Number(event.target.id);

    console.log(`buttonValue: ${buttonValue}`);
    console.log(`typeof buttonValue: ${typeof buttonValue}`);
    const buttonsClassName = event.target.className;
  
    if (buttonsClassName.includes("operator")) {
      console.log(`buttonClassName: ${buttonsClassName}`);
    }
    console.log(`%c typeof isButtonNumber: %c ${typeof isButtonNumber} `,'background:orange; color:yellow','background:yellow; color:orange; font-weight:bold');

    if (typeof isButtonNumber !== typeof Number) {
      console.log(`%c button pressed is NOT a number `,'background:black; color:red; font-weight:bold');
    }
    // dividend = 6
    // divisor = 2
    // quotient = 3
    // 6 / 2 = 3

    function operate() {
      if (calculation === undefined) {
        if (operator === "+") {
            setCalculation(Number(dividend) + Number(divisor));
        } else if (operator === "-") {
          setCalculation(Number(dividend) - Number(divisor));
        } else if (operator === "*") {
          setCalculation(Number(dividend) * Number(divisor));
        } else if (operator === "/") {
          setCalculation(Number(dividend) / Number(divisor));
        } else if (operator === "%") {
          setCalculation(Math.round((Number(dividend) / Number(divisor) * 100)));
        }
      } else if (calculation !== undefined) {
        if (operator === "+") {
          setCalculation(prevCalculation => [...prevCalculation,  Number(dividend) + Number(divisor)] );
        } else if (operator === "-") {
          setCalculation(prevCalculation => [...prevCalculation,  Number(dividend) - Number(divisor)] );
        } else if (operator === "*") {
          setCalculation(prevCalculation => [...prevCalculation,  Number(dividend) * Number(divisor)] );
        } else if (operator === "/") {
          setCalculation(prevCalculation => [...prevCalculation,  Number(dividend) / Number(divisor)] );
        } else if (operator === "%") {
          setCalculation(prevCalculation => [...prevCalculation, Math.round((Number(dividend) / Number(divisor) * 100))]);
        }
      }
    }

    console.log(`%c buttonId: %c ${buttonId} `,'background:orange; color:black','background:black; color:orange; font-weight:bold');
    console.log(`%c typeof buttonId: %c ${typeof buttonId} `,'background:black; color:orange','background:orange; color:black; font-weight:bold');

    
    if (calculation !== undefined && calculation !== null) {
      console.log(`%c calculation: %c ${calculation} `,'background:green; color:blue','background:blue; color:green; font-weight:bold');
      console.log(`%c calculation.length: %c ${calculation.length} `,'background:blue; color:green','background:green; color:blue; font-weight:bold');
    }

    // equals button has been pressed
    if (buttonId === "=") {
      operate();
    }

    // AC button
    if (buttonId == "AC") {
      setCalculation(undefined);
      setDividend(undefined);
      setDivisor(undefined);
      setOperator(undefined);
      return;
    }

    // DIVIDEND
    if (operator === undefined && divisor === undefined && !buttonsClassName.includes("operator")) {
      console.log(`dividend`);

      // new calculation
     if (dividend === undefined && !buttonsClassName.includes("operator") && operator === undefined) {
      setDividend(buttonValue);

        // if entering number < 1
      } else if (dividend === "0" && buttonId === ".") {
        setDividend(prevDividend => prevDividend + buttonValue);

        // dividend is NOT a decimal
      } else if (dividend !== "0" && dividend !== "0." && !buttonsClassName.includes("operator") && operator === undefined && divisor === undefined) {
        setDividend(prevDividend => prevDividend + buttonValue);
        console.log('dividend !== "0"');
      } 
    }

    // OPERATOR
    if (dividend !== undefined && divisor === undefined && buttonsClassName.includes("operator")) {
      console.log(`operator`);
      if (buttonsClassName.includes("operator") && divisor === undefined) {
        console.log(`operator entered`);
        setOperator(buttonValue);
      }  
    }

    // DIVISOR
    if (dividend !== undefined && operator !== undefined && !buttonsClassName.includes("operator")) {
      console.log(`divisor`);
      
      // divisor setting
      if (operator !== undefined && !buttonId.includes("operator") && divisor === undefined && buttonId !== "=") {
        setDivisor(buttonValue);
      } else if (operator !== undefined && !buttonId.includes("operator") && divisor !== undefined && buttonId !== "=") {
        setDivisor(prevDivisor => prevDivisor + buttonValue);
      }
    }

    // CALCULATION
    if (divisor !== undefined) {
      console.log(`calculation`);
      if (calculation !== undefined && buttonsClassName.includes("operator")) {
        setDividend(calculation);
        setOperator(buttonValue);
        setDivisor(undefined);
      }
    }
  }

  function deleteCalculate() {
    let lastInput;
    if (dividend !== undefined && operator === undefined)  {
      if (Object.values(dividend).length === 1) {
        console.log(`Object.values(dividend): %c${Object.values(dividend)}`,'color:red');
        setDividend(undefined);
      } else {
        lastInput = Object.values(dividend).pop();
        console.log(`lastInput: %c${lastInput}`,'color:red');
        setDividend(lastInput);
      }
     
    } else if (operator !== undefined && divisor === undefined) {
     setOperator(undefined);

    } else if (divisor !== undefined && calculation === undefined) {
      if (Object.values(divisor).length === 1) {
        console.log(`Object.values(divisor): %c${Object.values(divisor)}`,'color:red');
        setDivisor(undefined);
      } else {
        lastInput = Object.values(divisor).pop();
        console.log(`lastInput: %c${lastInput}`,'color:red');
        setDivisor(lastInput);
      }

    } else if (calculation !== undefined) {
     setCalculation(undefined);
    }
  }

  function expandCategoryExpenses(event) {
    const expenseCategoryExpand = event.target.id;
    setShowFocusCategory(prevState => !prevState);
    setShowAllCategories(prevState => !prevState);
    setFocusCategory(expenseCategoryExpand);
  }

  // FUEL UP ON...
  const [randoMeal, setRandoMeal] = useState();
  const [random, setRandom] = useState(false);
  const [meal, setMeal] = useState();
  const [ingredient, setIngredient] = useState();
  const [showIngredient, setShowIngredient] = useState(false);
  const [love, setLove] = useState("...LOVE");
  const foodSources = sources.energy;
  const mealPlans = plans.plans;
  const [minerals, setMinerals] = useState(false);
  const [plan, setPlan] = useState(false);
  const [planRoute, setPlanRoute] = useState();
  const [mealPlanData, setMealPlanData] = useState();
  const [selections, setSelections] = useState();

  // MEALDB RANDOM MEAL
  const randomMealUrl = "https://www.themealdb.com/api/json/v1/1/random.php";

  // search by ingredient
  const changeIngredientTarget = (event) => {
    const ingredientTarget = event.target.value;
    if (event.key === 'Enter') {
      setIngredient(ingredientTarget);
      setLove("...amor");

      if (ingredientTarget === undefined) {
        console.log(`%c ingredientTarget === undefined `, 'background:red; color: black');
      }
  
      if (ingredientTarget !== undefined){
        fetch(`https://www.themealdb.com/api/json/v1/1/filter.php?i=${ingredientTarget}`)
        .then(response => response.json())
        .then(response => {
          if (response.meals === null) {
            console.log(`response.meals === null`);
            fetch(`https://www.themealdb.com/api/json/v1/1/filter.php?i=${ingredientTarget}s`)
            .then(response => response.json())
            .then (response => {
              setMeal(response.meals);
              setMinerals(false);
              setRandom(false);
              setShowIngredient(true);
            })
          }
          setMeal((response.meals));
          setMinerals(false);
          setRandom(false);
          setShowIngredient(true);
          console.log(`response: %c ${response} `, 'background:orange; color:red; font-weight:bold');
          console.log(`response.meals: %c ${response.meals} `, 'background:red; color:orange; font-weight:bold');
          console.log(`Object.entries(response.meals): %c ${Object.values(response.meals)} `, 'background:orange; color:red; font-weight:bold');

          const mealThumbs = Object.entries(response.meals).map((meal) => {
            console.log(`map meal: %c ${Object.values(meal)} `,'background:orange; color:yellow')
            return meal;
          })

          console.log(`mealThumbs: %c ${mealThumbs}`,'color:green; background:orange');

          Object.values(mealThumbs).map((meall) => {
            console.table(`meall: %c ${meall} `,'background:green; color:blue');
            console.table(`Object.values(meall[1]): %c ${meall[1].strMealThumb} `,'background:green; color:blue');
            console.log(`Object.keys(meal) %c ${Object.keys(meall[1])} `,'background:green; color:orange');
          })
        })
        .catch(err => {
          console.error(err);
          setMeal([{strMeal: "Ingredient not found in database"}]);
          setMinerals(false);
          setRandom(false);
          setShowIngredient(true);
          setPlan(false);
        });
      }
    }
  }

  function fetchRandoMeal() {
    fetch(randomMealUrl)
    .then(response => response.json())
    .then(response => {
      setRandoMeal(response.meals[0]);
      setRandom(true);
      setMinerals(false);
      setShowIngredient(false);
      setPlan(false);
      const randoMealSplit = response.meals[0].strInstructions.split(". ");
      console.log(`response.meals[0].strInstructions: %c ${response.meals[0].strInstructions} `,'color:black; background:red; font-weight:bold');
      console.log(`randoMealSplit: %c ${randoMealSplit} `,'color:black; background:red; font-weight:bold');
      console.log(`typeof  randoMealSplit: %c ${typeof randoMealSplit} `,'color:black; background:red; font-weight:bold');
      console.log(`Object.values(randoMealSplit): %c ${Object.values(randoMealSplit)} `,'color:red; background:black; font-weight:bold');
    })
    .catch(err => console.error(err));
    setLove("...milosc");
  }

  function fetchMinerals() {
    setMinerals(true);
    setShowIngredient(false);
    setRandom(false);
    setPlan(false);
    setLove("...aimer");
  }

  function fetchPlan() {
    setMinerals(false);
    setShowIngredient(false);
    setRandom(false)
    setPlan(true);
    setLove("...aimer");
  }

  function fetchPlanRoute(event) {
    const planChoice = event.target.id;
    switch(planChoice) {
      case "cuisine":
        setPlanRoute(mealPlans[0]);
        setMealPlanData();
        break;
      case "type":
        setPlanRoute(mealPlans[1]);
        setMealPlanData();
        break;
      case "dietary needs":
        setPlanRoute(mealPlans[2]);
        setMealPlanData();
        break;
      default:
        setPlanRoute(undefined);
        break;
    }
  }

function showData(event) {
  const selected = event.target.id;
  const selectedIndex = selected.slice(-selected.length-1, 1);
  const selectedPath = selected.slice(1);

  console.log(`selectedIndex ${selectedIndex} selectedPath ${selectedPath}`);
  // console.log(`Object.keys(planRoute[Object.keys(planRoute)][selected]) ${Object.keys(planRoute[Object.keys(planRoute)][selected])}`);
  console.log(`selected: ${selected}`);
  console.log(`Object.keys(planRoute[Object.keys(planRoute)]) ${Object.keys(planRoute[Object.keys(planRoute)])}`);

  console.log(`Object.keys(planRoute[Object.keys(planRoute)]) ${Object.entries(planRoute[Object.keys(planRoute)][selectedIndex][selectedPath])}`);
  setMealPlanData(Object.values(planRoute[Object.keys(planRoute)][selectedIndex][selectedPath]));
  // setMealPlanData(planRoute[selected]);
  // getNextKeys(planRoute, currentSelections, selected);
}

  // XYZ visualizer of three[or more] variables

  // 

  // background theme adjuster according to local weather conditions

  // 

  // ERRANDS
  const [errands, setErrands] = useState({
    "home": [{}],
    "school": [{}],
    "medical": [{}],
    "work": [{}],
    "car": [{}],
    "leisure": [{}],
    "other": [{}]
  });
  const errandCategories = ["home", "school", "medical", "work", "car", "leisure", "other"];
  const errandTaskInput = useRef();
  const errandDueDateInput = useRef();
  const [scrollCount, setScrollCount] = useState(0);
  const [errandCategory, setErrandCategory] = useState("home");

  function categoryChange(){
    if (scrollCount === 6) {
      setScrollCount(0);
    } else {
      const scroll = scrollCount + 1;
      setScrollCount(scroll)
    }
    setErrandCategory(errandCategories[scrollCount]);
  }

  function onErrandUpdate(event) {
    event.preventDefault();

    let errandTaskItem = errandTaskInput.current.value;
    let errandDueDate = errandDueDateInput.current.value;

    if (errandTaskItem !== "" && errandDueDate !== "") {
      setErrands(prevErrands => ({ 
        ...prevErrands,
        [errandCategory]: [
          ...prevErrands[errandCategory],
          {[errandTaskItem]:errandDueDate}
        ]
      }))
    }
  }
  

  return (
    <Routes>
      <Route 
        path="/" 
        element={
        <Home
          onChange={changeContent}
          contentType={contentType}
          resume={resume}
        />}
      />
      <Route 
        path="signIn" 
        element={
        <SignIn
        />}
      />
      <Route 
        path="budget" 
        element={
        <Budget 
          budget={budget}
          expense={expense}
          // bills={bills}
          expenseTotal={expenseTotal}
          expenseItemRef={expenseItemRef}
          expenseAmountRef={expenseAmountRef}
          expenseCategoryRef={expenseCategoryRef}
          budgetEnter={budgetEnter}
          showBudgetInfo={showBudgetInfo}
          showExpenseInfo={showExpenseInfo}
          calculateCategorySubtotal={calculateCategorySubtotal}
          expandCategoryExpenses={expandCategoryExpenses}
          showAllCategories={showAllCategories}
          showFocusCategory={showFocusCategory}
          focusCategory={focusCategory}
          handleBudgetSubmit={handleBudgetSubmit}
          handleExpenseSubmit={handleExpenseSubmit}
          showCALC={showCALC}
          showCalculator={showCalculator}
          calculate={calculate}
          calculation={calculation}
          dividend={dividend}
          divisor={divisor}
          operator={operator}
          deleteCalculate={deleteCalculate}
        />}
      />
      <Route 
        path="errands" 
        element={
        <Errands 
          errands={errands}
          errandCategory={errandCategory}
          categoryChange={categoryChange}
          onErrandUpdate={onErrandUpdate}
          errandTaskInput={errandTaskInput}
          errandDueDateInput={errandDueDateInput}
          />} 
      />
      <Route 
        path="fuel" 
        element={
        <Fuel 
          meal={meal} 
          randoMeal={randoMeal} 
          changeIngredientTarget={changeIngredientTarget} 
          love={love} 
          fetchRandoMeal={fetchRandoMeal}
          sources={foodSources}
          minerals={minerals}
          fetchMinerals={fetchMinerals}
          showIngredient={showIngredient}
          random={random}
          fetchPlan={fetchPlan}
          mealPlans={mealPlans}
          plan={plan}
          // cuisine={cuisine}
          // type={type}
          // diet={diet}
          showData={showData}
          fetchPlanRoute={fetchPlanRoute}
          planRoute={planRoute}
          mealPlanData={mealPlanData}
          // readR={readR}
          selections={selections}
        />
        } 
      />
    </Routes>
  );
}

export default App;
