import '../../css/fuel.styles.css';

function FuelIn({ fetchMinerals, changeIngredientTarget, fetchRandoMeal, fetchPlan, random, minerals, plan }) {
  return (
    <div id="fuel-in">
      <div id="fuel-in-type" className="fuel-input-group">
        {/* <img
          className="type-symbol"
          alt="type icon"
          src={Ibeam}
        /> */}
        <input
          type="text"
          className="fuel-input highlight-item"
          placeholder="ingredient"
          id="meal-by-ingredient"
          onKeyUp={changeIngredientTarget}
        />
      </div>

      <div id="fuel-in-click" className="fuel-input-group">
        {/* <img
          className="type-symbol"
          alt="finger point icon"
          src={HandPointer}
        /> */}
        <button 
          type="button" 
          onClick={fetchRandoMeal} 
          className={`output-button  highlight-item ${random ? 'highlighted' : null}`}
        >
          random
        </button>
        <button
          type="button"
          onClick={fetchMinerals}
          className={`output-button  highlight-item ${minerals ? 'highlighted' : null}`}
        >
          minerals
        </button>
        <button
          type="button"
          onClick={fetchPlan}
          className={`output-button  highlight-item ${plan ? 'highlighted' : null}`}
        >
          plan
        </button>
      </div>
    </div>
  )
}

export default FuelIn