import React from 'react';
import FuelSources from './fuel-sources.component';
import MealPlans from './meal-plans.component';
import MealPlan from './meal-plan.component';
import '../../css/fuel.styles.css';
import error from '../../assets/404\ error.svg';

function FuelOut({ meal, randoMeal, random, showIngredient, minerals, sources, plan, mealPlans, fetchPlanRoute, cuisine, type, diet, planRoute, showData, mealPlanData, readR, selections}) {

    let randoMealSplit;

    if (randoMeal !== undefined) {
        randoMealSplit = randoMeal.strInstructions.split(". ");
    }

    return (
    <div className="fuelout">
        {
        random &&
            <div className="randoMeal">
                <h4 id="randoMeal-name"><u>{randoMeal.strMeal}</u></h4>
                <p id="randoMeal-area-category">{randoMeal.strArea} / {randoMeal.strCategory}</p>
                <ul id="randoMeal-instructions">
                    {randoMealSplit.map((instruction, index) => {
                        return <li key={index}
                                    className="randoMeal-instruction"
                                >
                                    {instruction}
                                </li>
                    })}
                </ul>
            </div>
        }

        {
        showIngredient &&
            <div className="ingredientMeal">
                {meal !== undefined && meal.length > 1 &&
                <>
                {meal.map((dish, index) => {
                        return <div key={index} className="ingredientMealItem">
                            <h4 className="dish-title"><u>{dish.strMeal}</u></h4>
                            
                            <img
                                className="meal-img"
                                alt={dish.strMeal}
                                src={dish.strMealThumb}
                                />
                            </div>
                    })
                }
                </>
                }
                {meal !== undefined && meal.length <= 1 &&
                    <div id="no-ingredient" className="ingredientMealItem">
                        <h4>{meal[0].strMeal}</h4>
                        <img
                            className="meal-img"
                            alt={meal.strMeal}
                            src={error}
                        />
                    </div>
                }
            </div>
        }

        {
        minerals &&
            <div>
                <FuelSources sources={sources} />
            </div>
        }

        {plan &&
            <div id="meal-plan-selections">
                <div id="meallz">
                    {Object.values(mealPlans).map((plan, index) => {

                        return <p 
                                key={index}
                                id={`${Object.keys(plan)}`}
                                className={`${mealPlans[Object.keys(plan)] ? 'highLight meal-plan-route' : 'meal-plan-route'}`}
                                onClick={fetchPlanRoute}
                                >
                                    {/* cuisine / type / dietary needs */}
                                    {Object.keys(plan)}
                                </p>
                    })}
                </div>

                {planRoute &&
                <div>
                    {/* <MealPlan 
                        planRoute={planRoute} 
                        showData={showData} 
                        mealPlanData={mealPlanData} 
                    /> */}
                     <MealPlans 
                        planRoute={planRoute} 
                        showData={showData} 
                        mealPlanData={mealPlanData} 
                        readR={readR}
                        selections={selections}
                    />
                </div>}
            </div>
        }
    </div>
    )
};

export default FuelOut;