import '../../css/fuel.styles.css';
import FuelIn from './fuel-in.component';
import FuelOut from './fuel-out.component';
import Main from '../../components/main.component';

function Fuel({ 
  ingredient, 
  meal, 
  randoMeal, 
  fetchRandoMeal, 
  love, 
  changeIngredientTarget, 
  sources, 
  minerals, 
  fetchMinerals,
  showIngredient,
  random,
  highlightInputs,
  fetchPlan,
  mealPlans,
  plan,
  cuisine,
  type,
  diet,
  fetchPlanRoute,
  planRoute,
  showData,
  mealPlanData,
  readR,
  selections
}) {

  return (
    <div className="fuel-depot">
      <Main />
      <h1 className="fuel-title">Fuel up on...</h1>
      <FuelIn 
        ingredient={ingredient} 
        changeIngredientTarget={changeIngredientTarget} 
        fetchRandoMeal={fetchRandoMeal}
        fetchMinerals={fetchMinerals}
        random={random}
        minerals={minerals}
        fetchPlan={fetchPlan}
        plan={plan}
      />
        <FuelOut 
          meal={meal}
          randoMeal={randoMeal} 
          ingredient={ingredient}
          sources={sources}
          minerals={minerals}
          mealPlans={mealPlans}
          plan={plan}
          cuisine={cuisine}
          type={type}
          diet={diet}
          fetchPlanRoute={fetchPlanRoute}
          showIngredient={showIngredient}
          random={random}
          highlightInputs={highlightInputs}
          planRoute={planRoute}
          showData={showData}
          mealPlanData={mealPlanData}
          readR={readR}
          selections={selections}
        />
      
      <p className="final-inspirational">{love}</p>
    </div>
  )
}

export default Fuel