import React from 'react'
import '../../css/fuel.styles.css';

function FuelSource({element, benefits, amount, rate, caution, source}) {
  
  return (
    <div className="fuel-source">
        <h3 className="mineral">{element}</h3>
        <p className="dosage">{amount} / {rate}</p>
        <ul>
          {benefits.map((benefit, index) => {
            return <li key={index} className="fuel-source-list">{benefit}</li>
          })}
        </ul>
      <ul>
        {source.map((sources, index) => {
          return <li key={index} className="fuel-source-list">{sources}</li>
            })}
            <p>{caution}</p>
      </ul>
    </div>
    )
  }
    
    export default FuelSource
